.ResultLine {
  font-size: 1.2rem;
}

.ResultLine td {
  padding: 4px 0;
}


.ResultLine__mult {
  color: rgb(204, 124, 23);
}

.ResultLine__clickable {
  padding: 10px;
  border-radius: 4px;
}

.ResultLine__table {
  border-collapse: collapse;
}

.ResultLine__clickable-row {
  border: #999999 1px dashed;
  cursor: pointer;
}
